<template>
    <div ref="PdfScrollReaderViewer" class="PdfScrollReaderViewer" :class="{ uiHidden }">
        <DynamicScroller
            v-if="canLoadPages"
            :items="rawChunkedPagesGroups"
            :keyField="'pageNumber'"
            :minItemSize="rawChunkedPagesGroups[0].height"
            :buffer="1000"
            ref="scroller"
            class="scroller"
        >
            <template v-slot="{ item, index, active }">
                <DynamicScrollerItem :item="item" :active="active" :data-index="index" :watchData="true">
                    <PdfScrollReaderViewerPage
                        class="PdfScrollReaderViewerPage"
                        v-observe-visibility="{
                            callback: (isVisible, entry) => visibilityChanged(isVisible, entry, item.pageNumber),
                            throttle: 100,
                            intersection: {
                                root: $refs.PdfScrollReaderViewer,
                                rootMargin: intersectionRootMargin,
                            },
                        }"
                        v-if="active"
                        :key="item.pageNumber"
                        :scaledSize="item.scaledSize"
                        :scaledSizeScale="item.scaledSizeScale"
                        :predominantColor="item.predominantColor"
                        :pageNumber="item.pageNumber"
                        :pageLargeUrl="item.large"
                        :pageTextLayerUrl="item.text_layer"
                        :pageAnnotationLayerUrl="item.annotation_layer"
                        :isCurrentPage="active"
                    ></PdfScrollReaderViewerPage>
                </DynamicScrollerItem>
            </template>
        </DynamicScroller>
    </div>
</template>

<script>
'use strict';

import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';
import 'hammerjs';
import { debounce, throttle, chunk, memoize } from 'lodash';
import PdfScrollReaderViewerPage from './PdfScrollReaderViewerPage.vue';
import isTouchDevice from '_shared_/js/utils/isTouchDevice.js';
import { DynamicScroller, DynamicScrollerItem } from 'vue-virtual-scroller';
import 'vue-virtual-scroller/dist/vue-virtual-scroller.css';
import { ObserveVisibility } from 'vue-observe-visibility';
import elementReady from 'element-ready';

export default {
    name: 'pdf-paged-reader-viewer',
    components: {
        PdfScrollReaderViewerPage,
        DynamicScroller,
        DynamicScrollerItem,
    },
    directives: {
        'observe-visibility': ObserveVisibility,
    },
    data() {
        return {
            focusedPage: null,
            pageBeforeZoom: null,
            willJumpPage: false,
            reachedSecondPage: false,
            scrollTop: 0,

            startedSwipeOverSelectionLayer: false,
            canLoadPages: false,

            hammer: null,

            maxScale: 2,
            scale: 1,
        };
    },
    computed: {
        ...mapState(['device', 'isLoading', 'uiHidden', 'publication', 'touch', 'ui']),
        ...mapGetters([
            'currentPage',
            'currentPages',
            'onlyMobileVisible',
            'doublePageMode',
            'publicationNumberOfPages',
            'isBuyableIssue',
        ]),
        intendedNumberOfPages() {
            return this.doublePageMode ? 2 : 1;
        },
        rawChunkedPagesGroups() {
            // console.log('calculated rawChunkedPagesGroups')
            // console.log(this.publication.files_urls);
            // If we don't do this, then we actually change the original array when doing the .shift()
            const pages = Object.assign([], this.publication.files_urls);
            let groups = [];
            if (this.intendedNumberOfPages === 2) {
                groups = [[{ pageNumber: 1, ...pages.shift() }]];
            } else {
                groups = [];
            }
            const chunks = chunk(pages, this.intendedNumberOfPages);
            // console.log('this.intendedNumberOfPages', this.intendedNumberOfPages);
            chunks.forEach((chunk, index) => {
                let group = [
                    // This already sums `1`, to take into account the first page
                    { pageNumber: (index + 1) * this.intendedNumberOfPages, ...chunk[0] },
                ];
                // If there're 2 pages in this chunk, add the second one to the group
                if (chunk[1]) {
                    // This already sums `1`, to take into account the first page
                    group.push({ pageNumber: (index + 1) * this.intendedNumberOfPages + 1, ...chunk[1] });
                }
                // console.log('group', group);
                groups.push(group);
            });

            groups = groups.map((group) => {
                const processedGroup = group.map((page) => {
                    const pageNamePayload = page.large
                        .substring(page.large.lastIndexOf('/') + 1)
                        .replace('.jpg', '')
                        .split('_');
                    const oringinalSize = {
                        width: parseInt(pageNamePayload[2]),
                        height: parseInt(pageNamePayload[3]),
                    };
                    const predominantColor = `#${pageNamePayload[4]}`;
                    const scaledSize = this.pageScaledSize(oringinalSize.width, oringinalSize.height);
                    const scaledSizeScale = this.pageScaledSizeScale(oringinalSize, scaledSize);

                    return {
                        ...page,
                        pageNamePayload,
                        oringinalSize,
                        predominantColor,
                        scaledSize,
                        ...{ width: scaledSize.width },
                        ...{ height: scaledSize.height },
                        scaledSizeScale,
                    };
                });
                return processedGroup[0];
            });

            return groups;
        },
        deviceWidth() {
            return this.device.width * this.scale;
        },
        deviceHeight() {
            return this.device.height * this.scale;
        },
        /**
         * Calc page width max scale is necessary for avoid
         * crop page when user need zoom the page to 100% width.
         * If we change directly `maxScale` value, affect to regular zoom that is set in max 2.
         */
        pageWidthMaxScale() {
            return this.device.width / this.rawChunkedPagesGroups[0].width;
        },
        /**
         * When showing the reader UI, we have less area to calculate the intersection against
         */
        intersectionRootMargin() {
            const headerbarOffsetHeight = this.uiHidden ? 0 : this.ui.headerbar.offsetHeight;
            const toolbarOffsetHeight = this.uiHidden ? 0 : this.ui.toolbar.offsetHeight;
            return `-${headerbarOffsetHeight}px 0px -${toolbarOffsetHeight}px 0px`;
        },
    },
    watch: {
        currentPage: function (currentPage, lastCurrentPage) {
            // console.log('pageChanged', this.focusedPage, currentPage);
            if (this.focusedPage !== currentPage) {
                // console.log('will scrollToItem', this.focusedPage, currentPage, this.willJumpPage)
                // console.log('itemIndex', currentPage - 1);
                // this.willJumpPage = true;
                this.focusedPage = currentPage;
                this.$refs.scroller.scrollToItem(currentPage - 1);
            }
            this.$bus.$emit('pageChanged', { pageNumber: currentPage });

            /*
             * Show payments link when an user go to last page in the preview and
             * the issue need is purchable.
             */
            if (this.isBuyableIssue && currentPage === this.publicationNumberOfPages) {
                this.togglePaymentLinks();
            }
        },
        focusedPage: function (focusedPage) {
            if (focusedPage > 1 && !this.reachedSecondPage) {
                this.reachedSecondPage = true;
                this.toggleUi({ uiHidden: true });
            }
        },
        scrollTop: function (scrollTop) {
            if (scrollTop === 0) {
                this.reachedSecondPage = false;
                // Disabled beacuse it reacts slowly
                // the idea was to show the UI when reaching the top of the scroll area
                // this.toggleUi({ uiHidden: false });
            }
        },
        scale: function (scale) {
            // console.log('scale changed', scale);
            if (scale > 1) {
                this.zoomByType({ type: 'page-width' });
            }
            if (scale === 1) {
                this.zoomByType({ type: 'page-fit' });
            }

            if (!this.$refs.scroller) {
                return;
            }
            this.$refs.scroller.onScrollerResize();
            if (this.pageBeforeZoom) {
                // console.log('catch pageBeforeZoom', this.pageBeforeZoom, this.focusedPage);
                const page = (this.focusedPage = this.pageBeforeZoom);
                this.pageBeforeZoom = null;
                this.$nextTick(() => {
                    this.$refs.scroller.scrollToItem(page - 1);
                });
            }
        },
    },
    mounted() {
        this.setLoadingState({ isLoading: false });
        this.canLoadPages = true;

        // The DOM is ready in Vue's next tick
        this.$nextTick(() => {
            this.initTouch();
            if (this.focusedPage !== this.currentPage) {
                // this.willJumpPage = true;
                this.focusedPage = this.currentPage;
                (async () => {
                    const element = await elementReady('.scroller');
                    this.$refs.scroller.scrollToItem(this.currentPage - 1);
                })();
            }
        });
    },
    beforeDestroy() {
        // console.log('beforeDestroy');
        this.setLoadingState({ isLoading: true });
        // When exiting double page mode in mobile `this.hammer` won't be available in this hook
        if (this.hammer) {
            this.hammer.destroy();
        }
        this.$bus.$off('singleTap', this.toggleUi);
        // this.$bus.$off('doubleTap', this.handleDoubleTap);
        this.$bus.$off('zoomIn', this.handleZoomIn);
        this.$bus.$off('zoomOut', this.handleZoomOut);
        this.$bus.$off('zoomSelect', this.handleZoomSelect);
    },
    methods: {
        log(eventName, e) {
            if (!e.force) {
                return;
            }
            // console.log(eventName, e, this.focusedPage)
        },

        visibilityChanged(isVisible, entry, pageNumber, force = false) {
            if (entry.intersectionRatio <= 0) {
                return;
            }
            // console.log({
            //     rootBoundsHeight: entry.rootBounds.height,
            //     targetOffsetHeight: entry.target.offsetHeight,
            // });
            /**
             * When calculating the intersection ratio,
             * we take into account the percentage of a page
             * (entry.target.offsetHeight) that fits into the
             * viewport (entry.rootBounds.height)
             */
            const ration = (0.5 / this.scale) * (entry.rootBounds.height / entry.target.offsetHeight);
            // console.log({
            //     isVisible: isVisible,
            //     pageNumber: pageNumber,
            //     is_focused: entry.intersectionRatio >= ration ? 'is focused' : '',
            //     ration: ration,
            //     scale: this.scale,
            //     intersectionRatio: entry.intersectionRatio,
            //     entry: entry,
            //     intersectionRootMargin: this.intersectionRootMargin,
            // });
            if (entry.intersectionRatio <= ration) {
                return;
            }
            if (this.focusedPage !== pageNumber || force) {
                // console.log('will debouncedGoToPage');
                this.debouncedGoToPage(pageNumber);
            }
            if (this.focusedPage === 1) {
                this.scrollTop = this.$refs.scroller.$el.scrollTop;
            }
        },

        debouncedGoToPage: debounce(
            function (pageNumber) {
                // console.log('debouncedGoToPage', pageNumber);
                this.focusedPage = pageNumber;
                this.pageBeforeZoom = null;
                this.goToPage({
                    page: parseInt(pageNumber),
                    trigger: 'scoll',
                    // source: 'range paginator',
                });
            },
            500,
            {
                leading: false,
                trailing: true,
            }
        ),

        ...mapMutations([
            'setLoadingState',
            'toggleUi',
            'setTotalPages',
            'hideSelectionTool',
            'zoomByType',
            'setReaderLayout',
            'setPage',
            'rangePageSelect',
            'togglePaymentLinks',
        ]),
        ...mapActions(['goToPrevPage', 'goToNextPage', 'goToPage']),

        pageScaledSize(originalWidth, originalHeight) {
            let height;
            let width;
            const aspectRatio = originalHeight / originalWidth;

            width = Math.min(this.deviceWidth * 0.7, originalWidth);
            height = width * aspectRatio;

            if (height < this.deviceHeight) {
                width = this.deviceWidth;
                height = width * aspectRatio;
            }

            return {
                width: width,
                height: height,
            };
        },

        pageScaledSizeScale(oringinalSize, scaledSize) {
            // The scale of the processed page relative to the current device screen size
            const scale =
                this.deviceHeight > this.deviceWidth
                    ? scaledSize.width / oringinalSize.width
                    : scaledSize.height / oringinalSize.height;

            return scale;
        },

        initTouch() {
            this.initHammer();
            this.$bus.$on('singleTap', this.toggleUi);
            // this.$bus.$on('doubleTap', this.handleDoubleTap);
            this.$bus.$on('zoomIn', this.handleZoomIn);
            this.$bus.$on('zoomOut', this.handleZoomOut);
            this.$bus.$on('zoomSelect', this.handleZoomSelect);
        },

        initHammer() {
            delete Hammer.defaults.cssProps.userSelect;
            this.hammer = new Hammer.Manager(this.$refs.PdfScrollReaderViewer);
            this.hammer.add(new Hammer.Tap({ event: 'tap' }));
            this.handleTaps();
        },

        handleZoomIn() {
            // console.log('handleZoomIn');
            this.pageBeforeZoom = this.focusedPage;
            this.scale = Math.min(this.scale + 0.25, this.maxScale);
            // console.log('this.scale', this.scale);
        },
        handleZoomOut() {
            // console.log('handleZoomOut');
            this.pageBeforeZoom = this.focusedPage;
            this.scale = Math.max(1, this.scale - 0.25);
            // console.log('this.scale', this.scale);
        },
        handleZoomSelect(zoom) {
            this.pageBeforeZoom = this.focusedPage;
            if (zoom.type === 'page-fit') {
                this.scale = 1;
            }
            if (zoom.type === 'page-width') {
                this.scale = this.pageWidthMaxScale;
            }
        },

        handleDoubleTap(e) {
            // Disabled beacuse has no smooth transition and also does not go to the touch center
            // console.log('handleDoubleTap')
            // if (this.scale < this.maxScale) {
            //     this.scale = this.maxScale;
            // } else {
            //     this.scale = 1;
            // }
        },

        handleTaps() {
            // console.log('handleTaps');

            let tapTimeout = null;
            let lastTouchTime = null;
            this.hammer.on('tap', (e) => {
                // console.log(e, e.target.nodeName);
                if (e.target.classList.contains('SmartZoomPoint') || e.target.nodeName === 'MARK') {
                    return;
                }

                const timeDiff = lastTouchTime !== null ? e.timeStamp - lastTouchTime : 20000;
                if (timeDiff < 250) {
                    // console.log('doubleTap', e);
                    this.$bus.$emit('doubleTap', e);
                    clearTimeout(tapTimeout);
                } else {
                    tapTimeout = setTimeout(() => {
                        // console.log('singleTap', e);
                        if (this.ui.selectionTool.opened) {
                            this.hideSelectionTool();
                            return;
                        }
                        this.$bus.$emit('singleTap', e);
                    }, 260);
                }
                lastTouchTime = e.timeStamp;
            });
        },
    },
};
</script>

