<template>
    <div ref="pageWrapper" class="PdfScrollReaderViewerPage" :class="{ isCurrentPage }" :style="style">
        <div ref="backgroundLayer" class="backgroundLayer" :class="{ imgHasLoaded }">
            <img draggable="false" ref="backgroundLayerImage" :src="pageLargeUrl" />
        </div>

        <SmartZoomLayer :hideLayer="false" :pageNumber="pageNumber"> </SmartZoomLayer>

        <TextLayer
            v-if="renderTextLayer"
            :hideLayer="false"
            :pageNumber="pageNumber"
            :textLayerStyles="textLayerStyles"
            :pageTextLayerUrl="pageTextLayerUrl"
        ></TextLayer>
        <AnnotationLayer
            v-if="renderAnnotationLayer"
            :hideLayer="false"
            :pageNumber="pageNumber"
            :annotationLayerStyles="annotationLayerStyles"
            :pageAnnotationLayerUrl="pageAnnotationLayerUrl"
        ></AnnotationLayer>

        <LoadingSpinner v-show="!imgHasLoaded" />
    </div>
</template>

<script>
'use strict';

import { mapState, mapGetters, mapMutations } from 'vuex';
import TextLayer from './TextLayer.vue';
import AnnotationLayer from './AnnotationLayer.vue';
import SmartZoomLayer from '_shared_/UI/SmartZoom/SmartZoomLayer.vue';
import LoadingSpinner from '_shared_/UI/Utilities/LoadingSpinner.vue';
import isTouchDevice from '_shared_/js/utils/isTouchDevice.js';

export default {
    name: 'pdf-scroll-reader-viewer-page',
    components: {
        TextLayer,
        AnnotationLayer,
        SmartZoomLayer,
        LoadingSpinner,
    },
    props: {
        scaledSize: {
            type: Object,
            required: true,
            default: false,
        },
        scaledSizeScale: {
            type: Number,
            required: true,
            default: false,
        },
        pageNumber: {
            type: Number,
            required: true,
            default: false,
        },
        pageLargeUrl: {
            type: String,
            required: true,
        },
        pageTextLayerUrl: {
            type: String,
            required: false,
        },
        pageAnnotationLayerUrl: {
            type: String,
            required: false,
        },
        isCurrentPage: {
            type: Boolean,
            required: true,
            default: false,
        },
    },
    data() {
        return {
            /**
             * This is here because some old publications that
             * where converted using a previous process lack
             * the information embeded in the file name
             */
            imgHasLoaded: false,
        };
    },
    computed: {
        ...mapState(['polpoSessionInitiated', 'device', 'reader', 'selectionsHaveBeenFetched']),
        style() {
            return {
                width: `${this.scaledSize.width}px`,
                height: `${this.scaledSize.height}px`,
            };
        },
        renderTextLayer() {
            return (
                this.isCurrentPage &&
                this.polpoSessionInitiated &&
                this.selectionsHaveBeenFetched &&
                this.pageTextLayerUrl
            );
        },
        textLayerStyles() {
            return {
                transformOrigin: 'left top',
                // The 10px are to account for the padding added to pages in the scroll mode
                top: '10px',
                transform: `scale(${this.scaledSizeScale})`,
            };
        },
        renderAnnotationLayer() {
            return this.isCurrentPage && this.pageAnnotationLayerUrl;
        },
        annotationLayerStyles() {
            return {
                // The 10px are to account for the padding added to pages in the scroll mode
                transformOrigin: 'left 10px',
                transform: `scale(${this.scaledSizeScale})`,
            };
        },
    },
    watch: {},
    mounted() {
        // console.log('mounted', this.pageNumber);
        // console.log('mounted', this.pageNumber, this.page_urls);
        if (this.$refs.backgroundLayerImage.complete) {
            this.imgHasLoaded = true;
        } else {
            this.$refs.backgroundLayerImage.onload = (e) => {
                this.imgHasLoaded = true;
                // This is a hack to fix a weird bug in which pages wont become visible
                // until a interaction, even when they were already downloaded.
                if (this.isCurrentPage && this.$refs.backgroundLayerImage) {
                    this.$refs.backgroundLayerImage.style.zIndex = 1;
                }
            };
        }
    },
    beforeDestroy() {},
    methods: {},
};
</script>

<style lang="scss" scoped>
.PdfScrollReaderViewerPage {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
    padding: 10px 0;
    margin: 0;
    min-height: 100px;
}

.backgroundLayer {
    box-shadow: 0px 2px 2px 0px var(--theme-border-color);
    transition: box-shadow 150ms ease;
    &.imgHasLoaded {
        box-shadow: 0px 2px 2px 0px transparent;
    }

    img {
        opacity: 0;
        transition: opacity 150ms ease;
    }
    &.imgHasLoaded {
        img {
            opacity: 1;
        }
    }
}

img {
    width: 100%;
    height: 100%;
    pointer-events: none;
    user-select: none;
    // Prevents image to become blurry in old Safari versions (probably other browsers too)
    // transform: translateZ(0px);
}
</style>
